:root {
    --frame-color: #00ffee;
    --bloom-color: #00ffff;
    --bg: #222222;
    --text: #ffffff;
    --frame-width: 1px;
}

body {
    background: black;
    color: var(--text);
    font-size: 36px;
}

img,
video {
    max-width: 100%;
}

section {
    min-height: 100vh;
}

.App {
    padding: var(--frame-width);
    box-sizing: border-box;
    text-align: center;
}

.App-hero {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.App-hero-logo {
    flex-grow: 0;
    width: 1600px;
    max-width: 80%;
}

section {
    max-width: 100%;
    overflow: hidden;
    overflow-wrap: break-word;
}

input {
    margin: 20px;
    max-width: 80%;
}

@media (max-width: 768px) {
    .App-hero-logo {
        max-width: 110%;
    }
}
